<template>
  <div class="bgc">
    <nav-bar btitle="商品列表"></nav-bar>
    <van-tabs title-active-color="red" v-model="active">
      <van-tab title="综合">综合</van-tab>
      <van-tab><template #title> 价格<van-icon class='rotate' name="exchange" /> </template></van-tab>
      <van-tab title="新品">新品</van-tab>
    </van-tabs>
    <div class="bbox">
      <div   v-for="item in product" :key="item.id" @click="to(item.id)">
        <div class="box" >
          <div class="img">
            <img :src="'http://localhost:8081/api' + item.mainImage" alt="">
          </div>
          <div class="name">{{ item.name }}</div>
          <div class="price">{{ item.price }}</div>
        </div>
      </div>
    </div>

  </div>
</template>
<script>
import NavBar from '@/components/bar/NavBar'
export default {
  data() {
    return {
      active: 1,
      product: []
    }
  },
  components: {
    NavBar
  },
  created() {
    this.product.splice(0, 0, ...this.$store.state.product)
    console.log(this.product[0])
    console.log(this.product)

  },
  methods: {
    onClickLeft() {
      this.$router.push('/catalogue')
    },
    onClickRight() {
      this.isSearch = !this.isSearch
    },
    to(addr){
      this.$router.push(`/product/${addr}`)
    }
  }
}
</script>
<style scoped>
.bgc{
  background-color: #ccc;
}
.rotate {
  transform: rotate(90deg);
}

.bbox {
  display: flex;
  width:100%;
  flex-wrap: wrap;
}

.box {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 180px;
  height: 150px;
  margin-top:15px;
  margin-right:3px;
  margin-left:3px;
  padding-top: 10px;
  background-color: #fff;
  
}

.img {
  max-width: 100px;
  max-height: 200px;
}

.img img {
  width: 100%;
  height: auto;
}
</style>


