<template>
  <div class="box">
    <nav-bar btitle="我的收藏"></nav-bar>
    <div class="top-right-btn" @click="isManaged">
      <div class="left"></div>
      <div><span v-if="!isManage">收藏</span><span v-if="isManage">取消</span></div>
    </div>
    
    <van-grid :border="false" :column-num="2">
      <van-grid-item class="vgi-box" v-for="item, index in product" :key="item.id">
        <div class="vgi-icon" v-if="isManage">
          <van-icon name="like" v-if="isClick[index]" @click="isClicked(index)" />
          <van-icon name="like-o" v-if="!isClick[index]" @click="isClicked(index)" />
        </div>
        <van-image :src="'http://localhost:8081/api' + item.mainImage" />
        <p>{{ item.name }}</p>
        <p>{{ item.price }}</p>
      </van-grid-item>
    </van-grid>
    <div class="bottom">
      <div>
        <button v-if="!isSelectAll" @click="selectAll(true)">全选</button>
        <button v-if="isSelectAll" @click="selectAll(false)">取消</button>
      </div>
      <div class="b-right"><button @click="dele"> 删除</button></div>
    </div>
  </div>
</template>

<script>
import NavBar from '@/components/bar/NavBar'

export default {
  data() {
    return {
      isManage: false,
      isSelectAll:false,
      isClick: [],
      product: [],
    }
  },
  components: {
    NavBar
  },
  created() {
    this.init()
  },

  methods: {
    isManaged() {
      this.isManage = !this.isManage
    },
    isClicked(index) {
      this.isClick.splice(index, 1, !this.isClick[index])
      console.log(this.isClick)
    },
    dele() {
      for (let i = 0; i < this.isClick.length; i++) {
        if (this.isClick[i] === true) {
          this.$store.commit('deleteCollected', { id: this.product[i].id })
          console.log(this.$store.state.collected)
        }
      }
      this.init()
      console.log(this.product)
      console.log(this.isClick)
    },
    selectAll(state){
      if(state===true&&this.isManage===false){
        this.isManage=true
      }
      if(state===false&&this.isManage===true){
        this.isManage=false
      }
      this.isSelectAll=!this.isSelectAll
      this.isClick.fill(state)
      console.log(this.isSelectAll)
      console.log(this.isClick)
    },
    init() {
      this.product = []
      this.isClick = []
      for (let i = 0; i < this.$store.state.collected.length; i++) {
        this.product.push(this.$store.state.product.filter((item) => item.id === this.$store.state.collected[i])[0])
      }
      for (let i = 0; i < this.product.length; i++) {
        this.isClick[i] = false
      }
      console.log(this.product)
      console.log(this.isClick)
    }
  }
}
</script>

<style scoped>
.top-right-btn {
  display: flex;
  justify-content: space-between;
  margin-top: 5px;
  margin-right: 10px;
}
.box{
  position: relative;
  
}
.bottom{
  width: 100%;
  position: absolute;
  bottom:0px;
  display: flex;
  justify-content: space-between;
  padding-left:10px;
}
.b-right{
  margin-right:20px;
}
.vgi-box{
  position: relative;
}
.vgi-icon{
  position: absolute;
  top:5px;
  left:5px;
  z-index:20000;

}
</style>



