<template>
  <div>
    <van-search v-model="value" placeholder="请输入搜索关键词" />
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item>1</van-swipe-item>
      <van-swipe-item>2</van-swipe-item>
      <van-swipe-item>3</van-swipe-item>
      <van-swipe-item>4</van-swipe-item>
    </van-swipe>
    <van-grid :border="false" :column-num="4">
      <van-grid-item>
        <van-icon name="gift-o" size="40px" @click="to('/product')"/>
        <p>全部商品</p>
      </van-grid-item>
      <van-grid-item>
        <van-icon name="todo-list-o" size="40px" />
        <p>优惠卷</p>
      </van-grid-item>
      <van-grid-item>
        <van-icon name="send-gift-o" size="40px" @click="to('/detail')" />
        <p>积分商品</p>
      </van-grid-item>
      <van-grid-item>
        <van-icon name="comment-o" size="40px" />
        <p>积分签到</p>
      </van-grid-item>
    </van-grid>
    <div class="text-box">
      <p class="tb-l">限时拼团</p>
      <div></div>
    </div>
    <!-- 限时拼团 -->
    <van-grid :border="false" :column-num="4" class="product-box">
      <van-grid-item center v-for="item in product" :key="item.id">
        <div class="img">
          <img :src="'http://localhost:8081/api' + item.mainImage" alt="">
        </div>
        <p class="pb-name">{{ item.name }}</p>
        <p class="pb-price">{{ item.price }}</p>
      </van-grid-item>
    </van-grid>
    <tab-bar></tab-bar>
  </div>
</template>

<script>
import TabBar from '@/components/bar/TabBar'
export default {
  data() {
    return {
      value: '',
      product: []
    }
  },
  components: {
    TabBar,
  },
  created() {
    let that = this
    that.$store.dispatch('getProductById', { id: '12' })
    setTimeout(function () {
      that.product.splice(0,1,that.$store.state.product[0]) 
      that.product.splice(1,1,that.$store.state.product[1])
      console.log(that.product)
    }, 100)
  },
  methods: {
    to(addr) {
      this.$router.push(addr)
    },
    
  },
}
</script>

<style scoped>
.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  line-height: 150px;
  text-align: center;
  background-color: #39a9ed;
}

.img {
  width: 100px;
  height: 100px;
}
.hidden{
 opacity: 1;
}
.img img {
  width: 100%;
  height: 100%;
}

.text-box {
  display: flex;
  justify-content: space-between;
}

.tb-l {
  margin-left: 3px;
}

.product-box {
  display: flex;
  justify-content: center;
}

.pb-price {
  margin-top: -10px;
  color: #e1811a;
}
</style>


