<template>
  <div>
    <nav-bar btitle="商品详情"></nav-bar>
    <div class="box">
      <div class="img">
        <img :src="'http://localhost:8081/api' + product.mainImage" alt="">
      </div>
      <div class="intro">
        <div class="i-box">
          <div class="i-left">$ <span>{{ product.price }}</span></div>
          <div class="i-right">
            <div class="ir-left">
              <p>浏览量</p>
              <p>3</p>
            </div>
            <div class="ir-right">
              <div class="ir-left">
                <p>销量</p>
                <p>4</p>
              </div>
            </div>
          </div>
        </div>
        <div class="discribe">{{ product.subTitle }}</div>
      </div>
      <div class="details">

      </div>
    </div>
    <div class="bottom">
      <div class="b-left">
        <span>购物车</span>
      </div>
      <div class="b-midden">
        <span v-if="!isCollect" @click="setCollect">收藏</span>
        <span v-if="isCollect" @click="deleteCollect">取消收藏</span>
      </div>
      <div class="b-right">
        <div class="br-left">加入购物车</div>
        <div class="br-right">购买</div>
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from '@/components/bar/NavBar'

export default {
  data() {
    return {
      product: {},

    }
  },
  computed: {
    isCollect() {
      return this.$store.state.collected.includes(this.product.id)
    }
  },
  created() {
    let id = this.$route.params.id
    this.product = this.$store.state.product[id]
  },
  components: {
    NavBar
  },
  methods: {
    deleteCollect() {
      this.$store.commit('deleteCollected', { id: this.product.id })
    },
    setCollect() {
      this.$store.commit('setCollected', { id: this.product.id })
    }
  },
  updated(){
    console.log(this.$store.state.collected)
  }
}
</script>

<style scoped>
.box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}


.img {
  margin-top: 10px;
}

.img img {
  width: 100%;
  height: 100%;
}

.i-box {
  display: flex;
  justify-content: space-between;

}

.i-left {
  color: orange;
}

.i-right {
  display: flex;

}

.i-left span {
  font-size: 2rem;
}

.intro,
.norms,
.details,
.bottom {
  width: 100%;
  margin-top: 10px;
  height: 50px;
}

.bottom{
  display: flex;
  justify-content: space-around;
}

.b-right{
  display: flex;
  height:25px;
  
}
.br-left{
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  background-color: yellow;
  width:80px;
  padding-left:10px;
  padding-right:10px;
}
.br-right{
  background-color: red;
  width:80px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  padding-left:10px;
  padding-right:10px;
}

</style>


