<template>
  <div>
    <nav-bar></nav-bar>
    <div class="box">
      <!-- 左侧缩略图 -->
      <div class="left">
        <!--  v-on使用v-for的提供的index来传递状态,v-bind使用index来标识 -->
        <div class="l-content" v-for="url, index in tUrls" @click="setActive(index)"
          :class="{ 'l-content-active': index == active }">
          <img :src="'http://localhost:8081/api' + url" alt="">
        </div>
      </div>

      <!-- 中间大图 -->
      <div class="middle">
        <div class="m-content">
          <img :src="'http://localhost:8081/api' + tUrls[active]" alt="">
        </div>
      </div>

      <!-- 右侧商品信息 -->
      <div class="right">
        <div class="r-content"></div>
      </div>
    </div>
  </div>
</template>
<script>
import NavBar from '@/components/bar/NavBar'
export default {
  data() {
    return {
      active: 0, 
      mainUrl: '',
      tUrls: [],
    }
  },

  methods: {
    setActive(index) {
      this.active = index
    },
  },

  created() {
    let that = this
    // 按URL里的id查询商品信息
    let id = this.$route.params.id
    this.$axios.get(`/product/${id}`)
      .then(res => {
        let data = res.data.data
        // 得到大图和缩略图的地址
        that.mainUrl = data.mainUrl 
        that.tUrls = data.subImages.split(',')
      })
      .catch(err => console.log(err))
  },

  components: {
    NavBar
  }

}
</script>

<style scoped>
.box {
  display: flex;
  justify-content: space-around;
}

.l-content {
  display: flex;
  flex-direction: column;
  width: 100px;
  height: 100px;
}

.l-content-active {
  border: 1px solid red;
}

.m-content {
  margin-top:80px;
  width: 200px;
  height: 200px;
}

img {
  max-width: 100%;
  height: 100%;
}
</style>


