<template>
  <div>
    <van-nav-bar title="我的钱包" @click-left="onClickLeft" @click-right="onClickRight">
      <template #left>
        <span><van-icon  name="arrow-left" />返回</span>
      </template>
      <template #right>
        <van-icon v-if='!isSearch' name="search" size="18" />
        <van-search v-if='isSearch' v-model="value" placeholder="请输入搜索关键词" />
      </template>
    </van-nav-bar>
    <div class="p-info">
      <div class="balance">
        <div>{{ asset }}</div>
        <div>总资产(元)</div>
      </div>
      <div class="coupons">
        <div>{{ recharge }}</div>
        <div>累计充值(元)</div>
      </div>
      <div class="purchases">
        <div>{{ consume }}</div>
        <div>累计消费(元)</div>
      </div>
    </div>
    <van-cell-group>
      <div class="mycell" v-for="info in cell" ><span class="left"><van-icon name="setting-o"/>{{info}}</span><van-button round type="danger" size="small">充值</van-button></div>
    </van-cell-group>
  </div>
</template>
<script>
export default {
  data() {
    return {
      asset: 1,
      recharge: 1,
      consume: 1,
      isSearch:0,
      searchValue:'请输入',
      cell:[
        '充值$100赠送$10',
        '充值$1赠送$1000'
      ]
    }
  },
  methods: {
    onClickLeft() {
      this.$router.push('/person')
    },
    onClickRight() {
      this.isSearch=!this.isSearch
    }
  }
}
</script>
<style scoped>
.p-info {
  display: flex;
  width: 380px;
  height: 70px;
  margin-top:20px;
  margin-left: 4px;
  justify-content: space-around;
  align-items: center;
  background-color: #3f171f;
  color: #e23d64;
  border-radius: 10px;
}
.mycell{
  display:flex;
  justify-content: space-around;
  align-items: center;
  height:40px;
  margin-top:10px;
}
</style>


