<template>
      <van-nav-bar :title="btitle" @click-left="onClickLeft" @click-right="onClickRight">
      <template #left>
        <span><van-icon name="arrow-left" />返回</span>
      </template>
      <template #right>
        <van-icon v-if='!isSearch' name="search" size="18" />
        <van-search v-if='isSearch' v-model="value" placeholder="请输入搜索关键词" />
      </template>
    </van-nav-bar>
</template>

<script>
export default {
  data(){
    return {
      isSearch: 0,
    }
  },
  props:{
    btitle:String,
  },
  methods:{
    onClickLeft() {
      this.$router.go(-1)
    },
    onClickRight() {
      this.isSearch = !this.isSearch
    },
  }
}
</script>


