import Vuex from 'Vuex'
import Vue from 'vue'
import axios from '@/api/index'
Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    product: [], // 保存所有商品 [{id:"1",price:11},...]
    collected: [],// 保存被收藏商品id ['1','3',...]
    history:[]
  },
  mutations: {
    // 根组件 初始化vuex,获取全部商品
    initProduct(state, payload) {
      state.product = payload
    },

    // home组件 test
    getProductById(state, payload) {
      let product = state.product.filter(item => item.id === payload.id)[0]
      return product
    },

    // collect,info组件  增删收藏
    deleteCollected(state,payload){
      let id=payload.id
      let index = state.collected.findIndex((item)=>item===id)
      state.collected.splice(index,1)

    },
    setCollected(state,payload){
      state.collected.push(payload.id)
    }


  },
  actions: {
    // 根组件 初始化 vuex
    initProduct(context) {
      axios.post('/product', {
        size: 225,
        current: 1
      }).then((res) => {
        context.commit('initProduct', res.data.data)
      })
    },

    // home 组件 test
    getProductById(context, payload) {
      return context.commit('getProductById', payload)
    }
  }
})



