import Vue from 'vue'
import Router from 'vue-router'
import Person from '@/components/Person'
import Purse from '@/components/Purse'
import Catalogue from '@/components/Catalogue'
import Product from '@/components/Product'
import Collect from '@/components/Collect'
import Home from '@/components/Home'
import Details from '@/components/Details'
import SlideShow from '@/components/bar/SlideShow'
import Info from '@/components/Info'
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/home',
      name: 'home',
      component: Home // 首页
    },
    {
      path: '/person',
      name: 'person',
      component: Person // 个人页
    },
    {
      path:'/purse',
      name:'purse',
      component: Purse // 充值页
    },
    {
      path:'/catalogue',
      name:'catalogue',
      component: Catalogue // 商品分类页
    },
    {
      path:'/product',
      name:'product',
      component:Product // 全部商品页
    },
    {
      path:'/collect',
      name:'collect',
      component:Collect // 收藏页
    },
    {
      path:'/details/:id',
      name:'details',
      component:Details // 商品信息页
    },
    {
      path:'/product/:id',
      name:'info',
      component:Info // 购买商品页
    },
    {
      path:'/test',
      name:'test',
      component:SlideShow // 轮播图页
    }
  ]
})



