<template>
  <div>
    <div class="top">
      <div class="set"><van-icon size='20px' color='white' name="setting-o" /></div>
      <div class="u-info">
        <van-image round width="5rem" height="5rem" src="https://img01.yzcdn.cn/vant/cat.jpeg" />
        <span>{{ userName }}</span>
      </div>
      <div class="p-info">
        <div class="balance">
          <div>{{ balance }}</div>
          <div>余额</div>
        </div>
        <div class="coupons">
          <div>{{ coupons }}</div>
          <div>优惠卷</div>
        </div>
        <div class="purchases">
          <div>{{ purchases }}</div>
          <div>购买数</div>
        </div>
        <div class="integral">
          <div>{{ integral }}</div>
          <div>积分</div>
        </div>
      </div>
    </div>
    <van-grid class="van-grid" :column-num="5">
      <van-grid-item icon="cart-o" text="未支付" />
      <van-grid-item icon="logistics" text="待发货" />
      <van-grid-item icon="sign" text="待收货" />
      <van-grid-item icon="orders-o" text="待评价" />
      <van-grid-item icon="shopping-cart-o" text="已完成" />
    </van-grid>
    <van-cell-group>
      <div  v-for="name,index in cell" >
        <router-link class="mycell" :to="url[index]" >
          <span class="left"><van-icon name="setting-o"/>{{name}}</span>
          <van-icon class="right" name="setting-o"/>
        </router-link></div>
    </van-cell-group>
    <tar-bar></tar-bar>
  </div>
</template>
<script>
import TarBar from '@/components/bar/TabBar'
export default {
  data() {
    return {
      userName: 'wzz',
      balance: 1,
      coupons: 1,
      purchases: 1,
      integral: 1,
      cell:['地址管理','我的钱包','我的优惠','我的收藏','我的足迹','修改密码'],
      url:['','/purse','','/collect','','','']
    }
  },
  components:{
    TarBar
  }
}
</script>

<style scoped>
.top {
  flex-direction: column;
  display: flex;
  width: 100%;
  background-color: #ff1a49;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
}

.set {
  display: flex;
  justify-content: end;
  margin-top: 10px;
  margin-right: 10px;
}

.u-info {
  display: flex;
  justify-content: start;
  align-items: center;
  margin-left: 15px;
  margin-bottom: 15px;
  text-indent: 1rem;
}

.p-info {
  display: flex;
  width: 380px;
  height: 50px;
  margin-bottom: -15px;
  margin-left: 4px;
  justify-content: space-around;
  align-items: center;
  background-color: #3f171f;
  color: #e23d64;
  border-radius: 10px;
}
.van-grid{
  margin-top:25px;
}
.mycell{
  display:flex;
  justify-content: space-between;
  align-items: center;
  height:40px;
  margin-top:10px;
}
.left{
  margin-left:5px;
  margin-right:5px;
}
.right{
  margin-right:5px;
}
</style>


