<template>
  <div class="box">
    <div class="left">
      <button class="pre-btn" @click="toPre"><van-icon name="arrow-up" /></button>
      <button class="next-btn" @click="toNext"><van-icon name="arrow-down" /></button>
      <div class="slide">
        <div class="l1"></div>
        <div class="l2"></div>
        <div class="l3"></div>
        <div class="l4"></div>
        <div class="l5"></div>
      </div>
    </div>
    <div class="right" style="background-color:red"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: 1,
      bgcs: ['red', 'blue', 'green', 'yellow', 'brown'],
      t: '',
    }
  },
  mounted() {
    this.t = setInterval(this.next, 2000)
  },
  methods: {
    next() {
      let that = this
      if (that.active === 5) {
        let slide = document.querySelector('.slide')
        slide.setAttribute('style', 'transition:all 1.5s;')
        that.active = 0
        that.add(that.bgcs[that.active])
      }
      let range = -50 * that.active
      let slide = document.querySelector('.slide')
      slide.setAttribute('style', `transition:all 1.5s;transform:translateY(${range}px);`)
      that.add(that.bgcs[that.active])
      that.active++
    },
    add(color) {
      document.querySelector('.right').setAttribute('style', `background-color:${color}`)
    },
    toPre() {
      let that = this
      clearInterval(that.t)
      if (that.active === 0) { 
        return
      } else {
        that.active-=1
        document.querySelector('.slide').setAttribute('style', `transition:all 0.3s;transform:translateY(${-50 * that.active}px);`)
      }
      that.t = setInterval(this.next,2000)
    },
    toNext() {
      let that = this
      clearInterval(that.t)
      if (that.active === 5) {
        return
      } else {
        that.active+=1
        document.querySelector('.slide').setAttribute('style', `transition:all 0.3s;transform:translateY(${-50 * that.active}px);`)
      }
      that.t = setInterval(this.next, 2000)
    }
  } 
}

</script>

<style scoped>
.box {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #ccc;
  transition: all 1.5s ease-in-out;
}

.left {
  display: flex;
  flex-direction: column;
  position: relative;
  flex-shrink: 0;
  height: 50px;
  width: 50px;
  overflow: hidden;
}

.left div {
  min-height: 50px;
  min-width: 50px;
}

.slide {
  height: 250px;
}

.pre-btn {
  background-color: red;
  position: absolute;
  top: 1px;
  left: 20px;
  width: 10px;
  height: 10px;
  z-index: 20000;
}

.next-btn {
  background-color: red;
  position: absolute;
  bottom: 1px;
  left: 20px;
  width: 10px;
  height: 10px;
  z-index: 20000;
}

.l1 {
  background-color: red;
}

.l2 {
  background-color: blue;
}

.l3 {
  background-color: green;
}

.l4 {
  background-color: yellow;
}

.l5 {
  background-color: brown;
}

.right {
  height: 200px;
  width: 200px;
}
</style>



