<template>
  <div>
    <nav-bar btitle="商品分类"></nav-bar>
    <div class="cata-box">
      <div class="left">
        <div v-for="item in category.filter(value => value.parentId === '0')" @click="setActive(item.id)">
          <span v-if='item.id === active' class="left-active-block"></span>
          {{item.name }}
        </div>
      </div>
      <div class='right' @click="onClickSecCatalogue">
        <div v-for="secitem in secCategory[Number(active) - 1] ">
          {{ secitem.name }}
        </div>
      </div>
    </div>
    <tab-bar></tab-bar>
  </div>
</template>

<script>
import NavBar from '@/components/bar/NavBar'
import TabBar from '@/components/bar/TabBar'
import axios from 'axios'
export default {
  data() {
    return {
      category: [], // 商品目录
      secCategory: [], // 二级目录
      active: 1, // 被点击的一级目录id值

    }
  },
  components: {
    NavBar,
    TabBar
  },
  methods: {
    setActive(id) {
      this.active = id
    },
    onClickSecCatalogue(e) {
      let name = e.target.innerText
      this.$router.push(`/product/${name}`)
    }
  },
  mounted() {
    let _this = this;
    (function getCategory() {
      axios.get('http://localhost:8081/api/category')
      .then((res) => {
        _this.category = res.data.data.filter((value) => value.id < 55) // 获取商品目录
        for (let i = 0; i < 6; i++) {
          _this.secCategory[i] = _this.category.filter((value) => value.parentId === String(i + 1)) // 获取二级商品目录,商品分类是1-6,这里用数组存
        }
      })
    })();
  },
}
</script>
<style scoped>
.cata-box {
  margin-left: 5px;
  display: flex;
}

.left {
  margin-left: 10px;
  border-right: 1px solid #ccc;
  height: 90vh;
  padding-right: 10px;
}

.left-active-block {
  display: inline-block;
  width: 10px;
  height: 18px;
  margin-right: 2px;
  background-color: red;
  transform: translateY(3px);
}

.right {
  margin-left: 10px;
}
</style>


